.chatButtonContainer {
  position: fixed;
  bottom: 40px;
  right: 40px;
  z-index: 1000;
}

.chatButton {
  background-color: #8abef1;
  color: white;
  border: none;
  padding: 15px 20px;
  border-radius: 30px;
  cursor: pointer;
  font-size: 1.2rem;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.15);
  transition: background-color 0.3s;
}

.chatButton:hover {
  background-color: #4899ea;
}

.chatBox {
  position: fixed;
  bottom: 40px;
  right: 200px;
  width: 524px;
  height: 640px;
  background-color: white;
  border-radius: 12px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding: 20px;
}

.chatHeader {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 10px 0;
  border-bottom: 1px solid #ddd;
}

.headerLeft {
  display: flex;
  align-items: center;
}

.chatHeader .logo {
  margin-right: 14px;
  width: 25px;
  height: 30px;
}

.chatHeader h3 {
  margin: 0;
  font-size: 1.5rem;
}

.closeButton {
  background: none;
  border: none;
  font-size: 1.2rem;
  cursor: pointer;
}

.chatBody {
  flex-grow: 1;
  overflow-y: auto;
  padding: 12px 2px;
  background-color: #fafcff;
  display: flex;
  flex-direction: column;
}

.chatFooter {
  display: flex;
  padding: 10px 0;
  border-top: 1px solid #ddd;
}

.chatFooter input {
  flex-grow: 1;
  padding: 10px;
  height: 26px;
  border: 1px solid #ddd;
  border-radius: 4px;
  font-size: 18px;
}

.chatFooter button {
  background-color: #3f8afd;
  color: white;
  border: none;
  padding: 10px 20px;
  margin-left: 10px;
  border-radius: 4px;
  cursor: pointer;
  font-size: 18px;
}

.chatFooter button:hover {
  background-color: #adcff2;
}

.userMessage {
  background-color: #dce8fa;
  color: #000000;
  padding: 10px;
  margin: 6px;
  border-radius: 15px;
  max-width: 60%;
  display: inline-block;
  word-wrap: break-word;
  margin-left: auto;
  text-align: left;
}

.otherMessage {
  background-color: #e0e0e0;
  color: #000000;
  padding: 10px;
  margin: 10px;
  border-radius: 15px;
  max-width: 60%;
  display: inline-block;
  word-wrap: break-word;
  margin-right: auto;
  text-align: left;
}
